@import "../utils/variables.module.scss";

.home {
    height: calc(100vh - calc($HeaderHeight + $FooterHeight));
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    background-color: $MainBC;

    .content_home {
        width: calc(100% - $NavigatorWidth);
        padding: $PaddingBox;
        box-sizing: border-box;
        overflow-y: auto;
    }
}

.content_apropos {
    width: calc(100% - $NavigatorWidth);
    padding: $PaddingBox;
    box-sizing: border-box;
    overflow-y: auto;
}



